// 2. Base
.l-constrained {
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  position: relative;

  @media (min-width: $layout-breakpoint-xs) {
      width: 100%;
      max-width: $layout-breakpoint-md;
  }
}

.l-constrained-md {
  max-width: $layout-breakpoint-sm;
}
